export const query = graphql`
  query {
    allStrapiCategories(filter: { isActive: { eq: true }, level: { eq: 1 } }) {
      nodes {
        id
        level
        labelTa
        label
        slug
        image {
          childImageSharp {
            fixed(width: 200, quality: 100) {
              ...GatsbyImageSharpFixed_withWebp
            }
          }
        }
        childs {
          id
          labelTa
          label
          slug
          image {
            childImageSharp {
              fixed(width: 200, quality: 100) {
                ...GatsbyImageSharpFixed_withWebp
              }
            }
          }
        }
      }
    }
    banner: file(relativePath: { eq: "header/header.jpg" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fluid(maxWidth: 720, maxHeight: 300, quality: 100) {
          ...GatsbyImageSharpFluid
          srcSetWebp
          srcWebp
        }
      }
    }
  }
`;

